<template>
    <div class="safe-center">
        <van-nav-bar 
        title="安全中心"
        left-text="返回"
        left-arrow
        @click-left="back"
        />
        <Bg>
          <div class="safe-center-box" >
              <van-cell is-link @click="changePassword">
                    <template #title>
                        <span class="custom-title">修改密码</span>
                    </template>
                </van-cell>
                <van-cell is-link @click="cancelAccount">
                    <template #title>
                        <span class="custom-title">注销账号</span>
                    </template>
                </van-cell>
          </div>
        </Bg>
        <div class="logout">
            <van-button color="#0c34ba" round block @click="logout">退出登录</van-button>
        </div>
        <van-popup
        v-model="isChangePassword"
        closeable
        position="bottom">
            <div class="change-password-box">
                <div class="change-password-title">修改密码</div>
                <div class="change-password-content">
                    <van-field v-model="userPassword.userOldPassowrd" :type="isOldPasswordShow?'text':'password'" label="旧密码" placeholder="旧密码(首字符为字母)" right-icon="closed-eye" @click-right-icon="toggleTypeOld" />
                    <van-field v-model="userPassword.userNewPassword" :type="isNewPasswordShow?'text':'password'" label="新密码" placeholder="新密码(首字符为字母)" right-icon="closed-eye" @click-right-icon="toggleTypeNew" />
                    <van-button  block round class="change-password-btn" color="#0c34ba" @click="submitPassword">提 交</van-button>
                </div>
            </div>
        </van-popup>
    </div>
</template>

<script>
    import '../assets/less/safe.less'
    import Bg from '../components/Bg'
    import {utils} from '../assets/js/utils'
    import {mapMutations} from 'vuex'
    export default {
        name:"SafeCenter",
        data(){
            return {
                //是否修改密码
                isChangePassword:false,
                //是否显示旧密码
                isOldPasswordShow:false,
                //是否显示新密码
                isNewPasswordShow:false,
                //用户密码
                userPassword:{
                    userOldPassowrd:"",
                    userNewPassword:"",
                }
            }
        },
        methods:{
            //返回上一层
            back(){
                this.$router.push({name:"My"});
            },
            //退出登录
            logout(){
                let tokenString = this.$cookies.get("tokenString");
                //tokenString不存在，跳转到登录
                if(!tokenString){
                    return this.$router.push({name:'Login'});
                }
                //加载提示
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'POST',
                    url:this.baseUrl + '/logout',
                    data:{
                        appkey:this.appkey,
                        tokenString
                    }
                }).then( res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //tokenString不正确，跳转到登录
                    if(res.data.code == 700) {
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                        //跳转到登录界面
                        return this.$router.push({name:'Login'});
                    }
                    //tokenString正确
                    if(res.data.code == "F001"){
                        this.$cookies.remove("tokenString");
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                    }
                }).catch(err => {
                    //请求失败
                    //关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            //修改密码
            changePassword(){
                this.isChangePassword = true;
            },
            //注销账号
            cancelAccount(){
                this.$dialog.confirm({
                    title: '注销账号',
                    message: '是否确认注销账号，一旦注销无法恢复！',
                    })
                    .then(() => {
                        let tokenString = this.$cookies.get("tokenString");
                        if(!tokenString) {
                            return this.$router.push({name:'Login'});
                        }
                        //加载提示
                        this.$toast.loading({
                            message: '注销中...',
                            forbidClick: true,
                            duration:0
                        });
                        //发送登录请求
                        this.axios({
                            method:'POST',
                            url:this.baseUrl + '/destroyAccount',
                            data:{
                                appkey:this.appkey,
                                tokenString,
                            }
                        }).then(res => {
                            //请求成功关闭加载提示
                            this.$toast.clear();
                            if(res.data.code == "G001"){
                                //清除tokenString
                                this.$cookies.remove("tokenString");
                                this.changeUserData({});
                                //提示注销成功
                                this.$toast({
                                    message:res.data.msg,
                                    forbidClick:true,
                                    duration:1000
                                }); 
                            }else {
                                //注销失败
                                this.$toast({
                                    message:res.data.msg,
                                    forbidClick:true,
                                    duration:1000
                                });
                            }
                        }).catch(err => {
                            //请求失败
                            this.$toast({
                                message:err,
                                forbidClick:true,
                                duration:1000
                            })
                        })
                    })
            },
            //提价新密码
            submitPassword(){
                let tokenString = this.$cookies.get("tokenString");
                if(!tokenString) {
                    return this.$router.push({name:'Login'});
                }
                let userChangePasswordMsg = {
                    newPassword:{
                        value:this.userPassword.userNewPassword,
                        reg:/^[A-Za-z]\w{5,15}$/,
                        errorMsg:"密码支持字母数字_组合且首字符必须是字母"
                    },
                }
                //表单验证
                if(!utils.validForm(userChangePasswordMsg)){
                    return false;
                }
                //加载提示
                this.$toast.loading({
                    message: '修改中...',
                    forbidClick: true,
                    duration:0
                });
                //发送登录请求
                this.axios({
                    method:'POST',
                    url:this.baseUrl + '/updatePassword',
                    data:{
                        appkey:this.appkey,
                        tokenString,
                        password:this.userPassword.userNewPassword,
                        oldPassword:this.userPassword.userOldPassowrd
                    }
                }).then(res => {
                    //请求成功关闭加载提示
                    this.$toast.clear();
                    if(res.data.code == "E001"){
                        this.isChangePassword = false;
                        //清除tokenString
                        this.$cookies.remove("tokenString");
                        //提示修改成功
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                        
                    }else {
                        //修改失败
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                    }
                }).catch(err => {
                    //请求失败
                    this.$toast({
                        message:err,
                        forbidClick:true,
                        duration:1000
                    })
                })
            },
            //切换旧密码显示
            toggleTypeOld(){
                if(this.isOldPasswordShow){
                    this.isOldPasswordShow = false;
                }else {
                    this.isOldPasswordShow = true;
                }
            },
            //切换新密码显示
            toggleTypeNew(){
                if(this.isNewPasswordShow){
                    this.isNewPasswordShow = false;
                }else {
                    this.isNewPasswordShow = true;
                }
            },
            ...mapMutations(['changeUserData'])
        },
        components:{
            Bg,
        }
    }
</script>
